body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.columns.is-vcentered {
  align-items: center; }

.columns.is-fullheight {
  min-height: 100vh; }

.columns.is-fullwidth {
  min-width: 100vw; }

.column.is-vcentered {
  align-items: center; }

.column.is-centered {
  justify-content: center; }

.column.is-fullheight {
  min-height: 100vh; }

.column.is-fullwidth {
  min-width: 100vw; }

.pagesAnimation-enter {
  opacity: 0.01;
  -webkit-transform: translateY(100px);
          transform: translateY(100px); }

.pagesAnimation-enter.pagesAnimation-enter-active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
  -webkit-transition: opacity .5s ease, -webkit-transform .5s ease;
  transition: opacity .5s ease, -webkit-transform .5s ease;
  transition: transform .5s ease, opacity .5s ease;
  transition: transform .5s ease, opacity .5s ease, -webkit-transform .5s ease; }

.pagesAnimation-exit {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1; }

.pagesAnimation-exit.pagesAnimation-exit-active {
  -webkit-transform: translateY(-100px);
          transform: translateY(-100px);
  opacity: 0.01;
  -webkit-transition: opacity .10s ease, -webkit-transform .10s ease;
  transition: opacity .10s ease, -webkit-transform .10s ease;
  transition: transform .10s ease, opacity .10s ease;
  transition: transform .10s ease, opacity .10s ease, -webkit-transform .10s ease; }

